import React from "react";
import {useSpring, animated} from 'react-spring'

export default function Item(props) {

  const springProps = useSpring({opacity: 1, from: {opacity: 0}})
  

  return (
<animated.div className="item" style={springProps}>
      <label
        className="container"
        style={{
          textDecorationLine: props.taskCompleted ? "line-through" : null,
        }}
      >
        {props.text}
        <input
          type="checkbox"
          onClick={() => props.handleChecking(props.index)}
        />
        <span className="checkmark"></span>
      </label>
      <div className="edit">
        <button
          onClick={() => props.handleDeleteItem(props.index)}
          disabled={!props.taskCompleted}
        >
          Delete
        </button>
      </div>   
</animated.div>
  );
}
