import React, { Component } from "react";
import AddItem from "./components/AddItem";
import Item from "./components/Item";
import "./styles/main.css";
import welcomeImage from "./images/add-new-task.jpg";
import { useSpring, animated } from "react-spring";

class App extends Component {
  state = {
    todos: [],
    totalItems: 0,
  };

  addItem = (event) => {
    event.preventDefault();
    const nItems = this.state.todos.length + 1;
    this.setState({
      todos: [
        ...this.state.todos,
        {
          id: nItems,
          text: event.target.task.value,
          taskCompleted: false,
        },
      ],
      totalItems: this.state.totalItems + 1,
    });
    event.target.task.value = "";
  };

  handleChecking = (index) => {
    let neweTodos = [...this.state.todos];
    neweTodos[index] = {
      ...neweTodos[index],
      taskCompleted: !neweTodos[index].taskCompleted,
    };
    this.setState({ todos: neweTodos });
  };

  handleDeleteItem = (i) => {
    let neweTodos = this.state.todos.filter((item, index) => i !== index);
    this.setState({ todos: neweTodos });
  };

  render() {
    return (
      <div className="App">
        <header>
          <h1>Todo React App</h1>
        </header>
        <main>
          <AddItem addItem={this.addItem} />
          {this.state.todos.length > 0 ? (
            this.state.todos.map((item, index) => (
              <Item
                key={index}
                index={index}
                handleChecking={this.handleChecking}
                taskCompleted={item.taskCompleted}
                handleDeleteItem={this.handleDeleteItem}
                text={item.text}
              />
            ))
          ) : (
            <div className="welcome-container">
              <div className="welcome-text">
                <h2>Hello,Welcome to the App</h2>
                <p>
                  manage your day-to-day tasks in a uniquely simple way, to get
                  started type your task in the form above and click the add
                  button, it's that simple!
                </p>
              </div>
              <div className="welcome-image">
                <img src={welcomeImage} alt={"welcome to th App"} />
              </div>
            </div>
          )}
        </main>
      </div>
    );
  }
}

export default App;
